<template>
  <div id="home-work">
    <head-top title="我的任务"></head-top>
    <van-tree-select height="calc(100vh - 4.3rem)" :items="items"
      :main-active-index.sync="activeIndex" @click-nav="treeChanged">
      <template slot="content">
        <homework-list ref="tree0" v-if="activeIndex===0" :type-selected="0"></homework-list>
        <homework-list ref="tree1" v-else :type-selected="1"></homework-list>
      </template>
    </van-tree-select>
  </div>
</template>

<script>
import homeworkList from "./homeworkList";
import { emojValidator } from "@/utils/index";

export default {
  name: "homework",
  components: { homeworkList },
  data() {
    return {
      emojValidator,
      tabSelected: "0",
      tabOptions: [
        {
          label: "未完成",
          name: "0",
          taskList: [],
          pno: 0,
          pageSize: 8,
          total: 0,

          loading: false,
          error: false,
          finished: false
        },
        {
          label: "已完成",
          name: "1",
          taskList: [],
          pno: 0,
          pageSize: 8,
          total: 0,

          loading: false,
          error: false,
          finished: false
        }
      ],
      isLoading: false,

      activeIndex: 0,
      items: [{ text: "随堂练习" }, { text: "课件" }],
      reqFunc: null
    };
  },
  computed: {},
  beforeRouteEnter(to, from, next) {
    next(vm => {
      // 通过 `vm` 访问组件实例
      if (
        from.path.indexOf("/practiceView") > -1 ||
        from.path.indexOf("/coursewareComment") > -1
      ) {
        vm.tabSelected = "1";
      }
    });
  },
  mounted() {
    // this.typeSelected(0);
  },
  methods: {
    treeChanged(index) {
      setTimeout(() => {
        this.$refs["tree" + index].init();
      }, 100);
    }
  }
};
</script>

<style lang="less" scoped>
@font-title: 1.25rem;
@font-sub-title: 1rem;
@font-info: 0.875rem;
#home-work {
  width: 100%;
  height: 100vh;
  overflow-y: auto;
  /deep/ .van-tree-select__nav {
    // flex: initial;
    position: fixed;
    z-index: 9;
    height: 100%;
  }
  /deep/ .van-tree-select__content {
    overflow-y: initial;
    min-height: 100%;
    margin-left: 85px;
    width: calc(100% - 85px);
  }
}
</style>



