<template>
  <div class="home-work">
    <van-tabs v-model="tabSelected" @change="handleTabClick">
      <van-tab v-for="(tab, k) in tabOptions" :key="k" :title="tab.label" :name="tab.name">

        <van-pull-refresh v-model="isLoading" @refresh="refreshTab(k)" class="pull_fresh">
          <van-list v-model="tab.loading" :finished="tab.finished" finished-text="没有更多了"
            :error.sync="tab.error" error-text="请求失败，点击重新加载" @load="loadData(k)">
            <task-list v-if="tab.taskList && tab.taskList.length > 0" :data-list="tab.taskList"
              :state="tabSelected" @download="download" @doDeal="doPractice" @doView="viewPractice">
            </task-list>
          </van-list>
        </van-pull-refresh>

      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import TaskList from "@/components/r-task-list";
import { emojValidator } from "@/utils/index";

export default {
  name: "homework",
  components: { TaskList },
  props: {
    typeSelected: {
      type: Number,
      default: 0
    }
  },
  data() {
    return {
      emojValidator,
      tabSelected: "0",
      tabOptions: [
        {
          label: "未完成",
          name: "0",
          taskList: [],
          pno: 0,
          pageSize: 8,
          total: 0,

          loading: false,
          error: false,
          finished: false
        },
        {
          label: "已完成",
          name: "1",
          taskList: [],
          pno: 0,
          pageSize: 8,
          total: 0,

          loading: false,
          error: false,
          finished: false
        }
      ],
      isLoading: false
    };
  },
  computed: {
    // eslint-disable-next-line vue/no-dupe-keys
    // reqFunc() {
    //   return this.typeSelected === 0
    //     ? this.$mainHttp.classHour.getPracticeTaskList
    //     : this.$mainHttp.classHour.getCourseWareTaskList;
    // }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      // 通过 `vm` 访问组件实例
      if (
        from.path.indexOf("/practiceView") > -1 ||
        from.path.indexOf("/coursewareComment") > -1
      ) {
        vm.tabSelected = "1";
      }
    });
  },
  mounted() {},
  methods: {
    init() {
      this.tabOptions = [
        {
          label: "未完成",
          name: "0",
          taskList: [],
          pno: 0,
          pageSize: 10,
          total: 0,

          loading: false,
          error: false,
          finished: false
        },
        {
          label: "已完成",
          name: "1",
          taskList: [],
          pno: 0,
          pageSize: 10,
          total: 0,

          loading: false,
          error: false,
          finished: false
        }
      ];
      this.refreshTab(this.tabSelected === "0" ? 0 : 1);
    },
    loadData(k) {
      if (k === 0) {
        this.getUnFinishTaskList();
      } else {
        this.getFinishedTaskList();
      }
    },
    refreshTab(k) {
      if (Math.abs(k) === 0) {
        this.tabOptions[0].taskList.length = 0;
        this.tabOptions[0].pno = 0;
        this.tabOptions[0].total = 0;
        this.tabOptions[0].error = false;
        this.tabOptions[0].finished = false;
        this.getUnFinishTaskList();
      } else {
        this.tabOptions[1].taskList.length = 0;
        this.tabOptions[1].pno = 0;
        this.tabOptions[1].total = 0;
        this.tabOptions[1].error = false;
        this.tabOptions[1].finished = false;
        this.getFinishedTaskList();
      }
    },
    handleTabClick(tab, event) {
      if (Math.abs(tab) === 0) {
        if (this.tabOptions[0].taskList.length === 0)
          this.getUnFinishTaskList();
      } else {
        if (this.tabOptions[1].taskList.length === 0)
          this.getFinishedTaskList();
      }
    },
    getUnFinishTaskList() {
      let item = this.tabOptions[0];
      this.isLoading = true;
      item.loading = true;
      if (item.pno === 0 || item.total > item.pno * item.pageSize) {
        item.pno++;
      } else {
        this.isLoading = false;
        item.loading = false;
        item.finished = true;
        return;
      }
      if (item.pno === 1) item.taskList = [];
      let reqData = {
        pno: item.pno,
        psize: item.pageSize,
        estate: 1,
        stuID: this.$store.getters.studentID
      };
      let reqFunc =
        this.typeSelected === 0
          ? this.$mainHttp.classHour.getPracticeTaskList
          : this.$mainHttp.classHour.getCourseWareTaskList;
      reqFunc(reqData)
        .then(res => {
          if (res.Result) {
            item.total = res.rowsCount;
            if (res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(sub => {
                item.taskList.push(sub);
              });
            }
          }
          if (res.RowsCount) item.total = res.RowsCount;
          if (item.total <= item.pno * item.pageSize) item.finished = true;
          item.loading = false;
          this.isLoading = false;
        })
        .catch(() => {
          item.error = true;
          item.loading = false;
          this.isLoading = false;
        });
    },
    getFinishedTaskList() {
      let item = this.tabOptions[1];
      this.isLoading = true;
      item.loading = true;
      if (item.pno === 0 || item.total > item.pno * item.pageSize) {
        item.pno++;
      } else {
        this.isLoading = false;
        item.loading = false;
        item.finished = true;
        return;
      }

      if (item.pno === 1) item.taskList = [];
      let reqData = {
        pno: item.pno,
        psize: item.pageSize,
        estate: 2,
        stuID: this.$store.getters.studentID
      };
      let reqFunc =
        this.typeSelected === 0
          ? this.$mainHttp.classHour.getPracticeTaskList
          : this.$mainHttp.classHour.getCourseWareTaskList;
      reqFunc(reqData)
        .then(res => {
          if (res.Result) {
            item.total = res.rowsCount;
            if (res.Datas && res.Datas.length > 0) {
              res.Datas.forEach(sub => {
                item.taskList.push(sub);
              });
            }
          }
          if (res.RowsCount) item.total = res.RowsCount;
          if (item.total <= item.pno * item.pageSize) item.finished = true;
          item.loading = false;
          this.isLoading = false;
        })
        .catch(() => {
          item.error = true;
          item.loading = false;
          this.isLoading = false;
        });
    },
    doPractice(item) {
      this.$router.push(
        `/space/practiceDone/${item["CIPID"]}/${item["PLID"]}/${item["CPID"]}/${item["CHID"]}`
      );
    },
    download(item) {
      let fileUrl = this.$mainHttp.resourse.file(item["MaterialID"]);
      if (!window.webkit) {
        window.location.href = fileUrl;
        this.addCoursewareTask(item);
      } else {
        sendToAPP(FuncType.downloadFile, fileUrl)
          .then(res => {
            this.addCoursewareTask(item);
          })
          .catch(err => {});
      }
    },
    addCoursewareTask(item) {
      this.$mainHttp.courseware
        .postCoursewareTask({
          cID: item["CID"],
          stuID: this.$store.getters.studentID
        })
        .then(res => {
          if (res.Result) {
            this.$toast("数据下载成功！");
            this.refreshTab(0);
          }
        })
        .catch(err => {});
    },
    viewPractice(item) {
      this.$router.push(
        `/space/practiceView/${item["CIPID"]}/${item["PLID"]}/${item["CPID"]}/${item["CHID"]}`
      );
    },
    comment(item) {
      this.$router.push(
        `/space/coursewareComment/${item["CID"]}/${item["CourseID"]}`
      );
    }
  }
};
</script>

<style lang="less" scoped>
@font-title: 1.25rem;
@font-sub-title: 1rem;
@font-info: 0.875rem;
.home-work {
  background-color: #fff;
  /deep/.van-tabs__wrap {
    position: fixed;
    width: calc(100% - 85px);
    top: 4.3rem;
    z-index: 9;
    // height: 59px;
    .van-tab {
      span {
        font-size: 1.17rem;
        font-family: MicrosoftYaHei;
        font-weight: 400;
        color: rgba(0, 203, 171, 1);
      }
    }
    .van-tabs__line {
      width: 16vw;
      height: 1px;
      background: rgba(0, 203, 171, 1);
    }
  }

  /deep/ .van-tabs__content {
    padding-top: 59px;
  }
  /deep/.pull_fresh {
    height: 100%;
  }
}
</style>



